@media (min-width: 325px) {
  .simon {
    min-height: 900px; }
    .simon .gamepad table {
      background: white;
      box-shadow: 0px 0px 29px 9px #616161;
      border: 1px solid black;
      border-radius: 50%;
      text-align: center;
      padding: 20px 20px;
      border-collapse: separate; }
      .simon .gamepad table .bottomright {
        cursor: pointer;
        width: 150px;
        height: 150px;
        box-shadow: 0px 0px 4px 1px #858585;
        background: red;
        border-radius: 0 0 150px 0;
        -moz-border-radius: 0 0 150px 0;
        -webkit-border-radius: 0 0 150px 0; }
      .simon .gamepad table .bottomleft {
        cursor: pointer;
        width: 150px;
        height: 150px;
        box-shadow: 0px 0px 4px 1px #858585;
        background: blue;
        border-radius: 0 0 0 150px;
        -moz-border-radius: 0 0 0 150px;
        -webkit-border-radius: 0 0 0 150px; }
      .simon .gamepad table .topright {
        cursor: pointer;
        width: 150px;
        height: 150px;
        box-shadow: 0px 0px 4px 1px #858585;
        background: green;
        border-radius: 0 150px 0 0;
        -moz-border-radius: 0 150px 0 0;
        -webkit-border-radius: 0 150px 0 0; }
      .simon .gamepad table .topleft {
        cursor: pointer;
        width: 150px;
        height: 150px;
        box-shadow: 0px 0px 4px 1px #858585;
        background: orange;
        border-radius: 150px 0 0 0;
        -moz-border-radius: 150px 0 0 0;
        -webkit-border-radius: 150px 0 0 0; }
    .simon .gamepad .central-panel {
      background: white;
      width: 150px;
      height: 150px;
      position: absolute;
      top: 33%;
      border-radius: 50%; }
      .simon .gamepad .central-panel .title-panel {
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: bold;
        font-size: 25px;
        position: relative;
        top: 14%;
        left: 17%; }
      .simon .gamepad .central-panel .strict-hold {
        width: 70px;
        margin-top: 20px;
        position: relative;
        left: 30%; }
        .simon .gamepad .central-panel .strict-hold .text-muted {
          padding-left: 5px; }
        .simon .gamepad .central-panel .strict-hold .strict.btn.btn-danger.active {
          background-color: green;
          transition: 1s background-color linear; }
      .simon .gamepad .central-panel .button-panel {
        position: absolute;
        width: 80px;
        left: 25%;
        margin-top: 60%; } }

@media (min-width: 580px) {
  .simon .gamepad .central-panel {
    top: 37%; } }
