.calculator {
  background: linear-gradient(to right, #cf9413, #b9b8b8);
  color: rgba(255, 255, 255, 0.932);
  min-height: 1000px; }
  .calculator table, .calculator th, .calculator td {
    background: linear-gradient(to left, #cf9413, #b9b8b8);
    border: 1px solid black;
    text-align: center;
    padding: 20px 45px;
    border-collapse: separate; }
    .calculator table .display1, .calculator th .display1, .calculator td .display1 {
      background: #d8d8d8;
      color: black;
      font: 2em  bold;
      border-top: none;
      position: relative;
      top: -2px;
      text-align: right; }
    .calculator table .display2, .calculator th .display2, .calculator td .display2 {
      background: #d8d8d8;
      color: black;
      font: 0.875em;
      text-align: right;
      border-bottom: none;
      padding: 2px; }
